import React from 'react';
import AddOption from './AddOption';
import Header from './Header';
import Action from './Action';
import Options from './Options';
import OptionModal from './OptionModal';

export default class IndecisionApp extends React.Component {

	state = {
		options : [],
		selectedOption : undefined
	};


	handleDeleteOptions = () => {

		this.setState( () =>( { options : [], selectedOption : undefined } ));
	};

	handleDeleteOptionSingular = ( option ) => {

		this.setState(( priorState ) => ( {
				options : priorState.options.filter( ( opt ) =>  opt !== option )
			} )
		);
	};

	handlePick = () => {
		const randomNum = Math.floor( Math.random() * this.state.options.length );

		let selectedOption = this.state.options[ randomNum ];
		this.setState( () => ({ selectedOption }));
	};

	handleCloseModal = () => {
		this.setState( () => ( { selectedOption : undefined } ));
	};

	handleAddOption = ( option ) => {
		if( !option ) {
			return 'Enter a valid value to add an option';
		}
		else if( this.state.options.indexOf( option ) > -1 ){
			return 'This option already exists!';
		}

		this.setState( ( priorState ) => ({	options : priorState.options.concat( option )	}));
	};

	componentDidMount () {
		console.log( "Mounted" );
		try {
			const json = localStorage.getItem( "options" );
			const options = JSON.parse( json );
			if ( options ) {
				this.setState( () => ( { options } ) );
			}
		}
		catch( err ){
			console.error( err );
		}
	}

	componentDidUpdate ( priorProps, priorState ) {
		if( priorState.options.length !== this.state.options.length ) {
			const json = JSON.stringify( this.state.options );

			localStorage.setItem( "options", json );
		}
	}

	componentWillUnmount(){
		console.log( "Will Unmount" );
	}

	render() {
		const subTitle = "Put your life in the hands of a computer";

		return (
			<div>
				<Header subTitle={subTitle} />
				<Action
					selectedOption={this.state.selectedOption}
					handlePick={this.handlePick}
					hasOptions={this.state.options.length > 0}
					options={this.state.options} />
				<Options
					options={this.state.options}
					removeAllOptions={this.handleDeleteOptions}
					removeOne={this.handleDeleteOptionSingular}/>
				<AddOption callback={this.handleAddOption} />
				<OptionModal
					selectedOption={this.state.selectedOption}
					closeCallback={this.handleCloseModal} />
			</div>
		)
	}
}

IndecisionApp.defaultProps = {
	selectedOption : undefined
};
